.popup {
  z-index: 1000000;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  padding: 18px 10px 25px;
  place-items: center;
  background-color: hsla(0deg, 0%, 100%, 0.8);
  overflow: auto;
}
.popup--open {
  display: grid;
}

.popup__inner {
  position: relative;
  max-width: 400px;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.16);
}
@media (min-width: 1280px) {
  .popup__inner {
    width: 1000px;
    max-width: none;
    border-radius: 0;
  }
}

.popup__close {
  position: absolute;
  top: 3px;
  right: 4px;
  width: 40px;
  height: 40px;
  padding: 0;
  border: none;
  background-color: transparent;
  background-image: url("../img/icons/popup-close.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}

.no-scroll {
