.note {
  position: relative;
  padding: 6px 28px 8px;
  border: 1px solid #e0e0e0;
  border-radius: 3px;
  text-align: center;
  font-size: 13px;
  line-height: 13px;
  background-color: #ffffff;
}
@media (min-width: 375px) {
  .note {
    padding-right: 3px;
    padding-bottom: 6px;
    text-align: left;
  }
}
@media (min-width: 768px) {
  .note {
    padding: 10px 31px 9px;
  }
}
@media (min-width: 1280px) {
  .note {
    padding: 6px 20px 5px 31px;
  }
}

.note__accent {
  display: grid;
  margin: 0 0 1px;
  justify-content: center;
  column-gap: 10px;
  grid-auto-flow: column;
  font-size: 12px;
  line-height: 13px;
  font-weight: 500;
  letter-spacing: -0.01em;
}
@media (min-width: 375px) {
  .note__accent {
    justify-content: start;
  }
}
@media (min-width: 768px) {
  .note__accent {
    margin-bottom: 7px;
  }
}
@media (min-width: 1280px) {
  .note__accent {
    margin-bottom: 2px;
  }
}

.note__accent::before {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  background-image: url("../img/icons/note-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 375px) {
  .note__accent::before {
    position: absolute;
    top: 5px;
    left: 5px;
  }
}
@media (min-width: 768px) {
  .note__accent::before {
    top: 8px;
  }
}
@media (min-width: 1280px) {
  .note__accent::before {
    top: 5px;
  }
}

.note__accent strong,
.note__accent small {
  font: inherit;
}

.note__accent strong {
  margin: 0;
}

.note__text {
  padding: 0 0 0 26px;
  margin: 0 0 1px;
  font: inherit;
}
@media (min-width: 375px) {
  .note__text {
    padding-left: 0;
  }
}
@media (min-width: 1510px) {
  .note__text {
    margin-bottom: 3px;
  }
}

.note--alter {
  padding: 6px 10px 6px 31px;
  text-align: start;
}
.note--alter .note__accent {
  justify-content: start;
}
.note--alter .note__accent::before {
  position: absolute;
  top: 5px;
  left: 5px;
}
.note--alter .note__text {
  display: none;
}
@media (min-width: 1280px) {
  .note--alter .note__text {
    display: block;
  }
}
@media (min-width: 1280px) {
  .note--alter {
    padding: 5px 10px 6px 32px;
  }
}

