.number-field {
  display: grid;
  grid-template-columns: 20px auto 20px;
  justify-content: center;
}

.number-field__control {
  width: 50px;
  min-width: 50px;
  max-width: 130px;
  padding: 2px 4px 0;
  border: none;
  margin: 0;
  text-align: center;
  font-size: 16px;
  line-height: 20px;
  appearance: textfield;
}
.number-field__control::-webkit-inner-spin-button, .number-field__control::-webkit-outer-spin-button {
  appearance: none;
}
@media (min-width: 768px) {
  .number-field__control {
    font-size: 24px;
    line-height: 28px;
  }
}

.number-field__button {
  position: relative;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: left center;
  cursor: pointer;
}
.number-field__button::before {
  content: "";
  position: absolute;
  top: -4px;
  right: -4px;
  bottom: -4px;
  left: -4px;
}
.number-field__button--minus {
  order: -1;
  background-image: url("../img/icons/button-minus-icon.svg");
}
.number-field__button--plus {
  background-image: url("../img/icons/button-plus-icon.svg");
}

.number-field--big .number-field__control {
  font-size: 24px;
  line-height: 28px;
}

