.select-2 {
  position: relative;
  width: 158px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
}

.select-2__button {
  display: grid;
  width: 100%;
  padding: 5px 11px 3px;
  border: 1px solid #c8c8c8;
  border-radius: 3px;
  grid-template-columns: max-content 1fr auto;
  align-items: center;
  font: inherit;
  line-height: inherit;
  color: #808080;
  background-color: #ffffff;
  cursor: pointer;
}
.select-2__button::after {
  content: "";
  width: 10px;
  height: 6px;
  background-image: url("../img/icons/select-2-arrow-down.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.select-2__button-value {
  font-size: 16px;
  line-height: 24px;
  color: #333333;
}

.select-2__options {
  z-index: 2;
  position: absolute;
  right: 0;
  bottom: -7px;
  left: 0;
  visibility: hidden;
  max-height: 180px;
  padding: 15px 0 10px;
  border-radius: 3px;
  background-color: #ffffff;
  overflow-y: scroll;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  transform: translateY(100%);
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.select-2__options::-webkit-scrollbar {
  display: none;
}

.select-2__radiobutton-label {
  padding: 5px 15px;
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
}

.select-2__radiobutton-label {
  display: block;
}

.select-2__radiobutton-label:hover,
.select-2__radiobutton:checked + .select-2__radiobutton-label {
  color: #ffffff;
  background-color: #42bd98;
}

.select-2--open .select-2__button {
  border-color: #42bd98;
}
.select-2--open .select-2__button::after {
  transform: rotate(180deg);
}
.select-2--open .select-2__options {
  visibility: visible;
}

.loader {
  color: transparent;
  background-image: url("../img/icons/loader.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: auto 60%;
}